html {
}

body {
	font-family: 'Didact Gothic', Arial, Helvetica, sans-serif;
	font-size: 16px;
	color: white;
	background: radial-gradient(circle, rgb(255, 155, 155), rgb(255, 102, 102));
}

a {
	color: white;
	font-weight: bold;
}

a:hover {
	color: rgb(255, 102, 102);
}

.text-button {
	display: inline;
	padding: 0;
	border: none;
	color: rgb(0, 207, 155);;
}

.text-button:hover {
	background: none;
	color: rgb(255, 102, 102);
}

nav {
	background: rgb(0, 207, 155);
}

nav button {
	background: transparent;
	border: 1px solid transparent;
	border-radius: 2em;
	color: white;
	font-weight: bold;
}

nav button:hover {
	background: orange;
}

nav ul {
	display: inline-block;
	margin: 0 0;
}

nav ul li {
	font-size: 2em;
	display: inline-block;
	margin: 0 0.5em;
}

.display-1 {
	font-size: 3em;
}

.modal-dialog {
	color: black;
}

.modal-dialog a {
	color: rgb(0, 207, 155);
}

.modal-dialog a:hover {
	color: orange;
}

.login-form-container {
	display: none;
	position: fixed;
	left: 0;
	top: -100vh;
	background: rgba(0,0,0,0.6);
	transition: all 1s;
}

.login-form {
	display: block;
	position: relative;
	top: -100vh;
	min-width: 40vw;
	transition: all 1s;
}

.anim-slide-in-top {
	animation: slideInTop 1s;
	top: 0;
}

.anim-slide-out-top {
	animation: slideOutTop 1s;
	top: -100vh;
}

.lead {
	font-size: 1.5em;
}

.user-summary-card li {
	display: inline-block;
}

.user-name-text {
	font-size: 2em;
	font-weight: bold;
}

.user-id-text {
	font-size: 1.2em;
	font-weight: normal;
}

.profile-image {
	border: 1px solid transparent;
	border-radius: 50%;
	width: 5em;
	height: 5em;
	margin: 2vw 2vw;
}

.user-image-section {
	background: radial-gradient(circle, rgb(255, 255, 255), rgb(241, 241, 241));
}

.user-image {
	display: inline-block;
	overflow: hidden;
	border: 1px solid transparent;
	border-radius: 20%;
	width: 10em;
	height: 10em;
	margin: 1em 1em;
}

.user-summary-card {
	background: rgb(0, 207, 155);
}

@keyframes slideInTop {
	from {
		top: -100vh;
	} 
	to {
		top: 0;
	}
}

@keyframes slideOutTop {
	from {
		top: 0;
	} 
	to {
		top: -100vh;
	}
}